/* eslint-disable react/no-danger */
import dynamic from 'next/dynamic';
import { memo, useCallback, useEffect, useRef } from 'react';
import { I_Map, I_MapConfig, I_Scene } from '@containers/Home/types';
import { useSelector } from 'react-redux';
import { selectSceneSelected } from '@containers/Home/selectors';
import { ActionMapWrapper, PopupWrapper } from './styled';
import GoogleMap, { Type_RefFuncGoogleMap } from './GoogleMap';

const DialogContainer = dynamic(() => import('@components/DialogContainer'));

interface Props {
  visible: boolean;
  onCancel: () => void;
  mapData?: I_Map;
  markerSelected?: I_Map['markers'][number];
  configMap?: I_MapConfig;
  onChangeScene: (scene: I_Scene) => void;
}

export const Popup = ({
  visible,
  onCancel,
  markerSelected,
  mapData,
  configMap,
  onChangeScene,
}: Props) => {
  const googleMapRef = useRef<Type_RefFuncGoogleMap>(null!);

  const sceneSelected = useSelector(selectSceneSelected);

  const onCoverAllMarker = useCallback(() => {
    googleMapRef.current?.onFitBounds();
  }, []);

  const onCenterToMarkerActive = useCallback(() => {
    markerSelected && googleMapRef.current?.onPanToMarker(markerSelected);
  }, [markerSelected]);

  useEffect(() => {
    setTimeout(() => {
      googleMapRef.current?.onInvalidateSize();
    }, 100);
  }, []);

  return (
    <DialogContainer
      visible={visible}
      onCancel={onCancel}
      maskClosable
      centered
      width="100vw"
      bodyStyle={{ maxWidth: '960px', width: '100%' }}
    >
      <PopupWrapper>
        <ActionMapWrapper>
          <div className="--left" />
          <div className="--right">
            <div className="action--item" onClick={onCenterToMarkerActive}>
              <i aria-hidden className="far fa-crosshairs" />
            </div>
            <div className="action--item" onClick={onCoverAllMarker}>
              <i aria-hidden className="far fa-ball-pile" />
            </div>
          </div>
        </ActionMapWrapper>
        <GoogleMap
          ref={googleMapRef}
          mapData={mapData}
          sceneSelected={sceneSelected}
          markerSelected={markerSelected}
          configMap={configMap}
          onChangeScene={onChangeScene}
        />
      </PopupWrapper>
    </DialogContainer>
  );
};

export default memo(Popup);
